<template>
  <div>
    <div class="passWord_nav">
      <van-nav-bar title="找回密码">
        <template #left>
          <van-icon
            name="arrow-left"
            size="22"
            color="black"
            @click="toLogins"
          />
        </template>
      </van-nav-bar>
    </div>
    <div class="passWord_from">
      <van-form validate-first @failed="onFailed">
        <!-- 通过 pattern 进行正则校验 -->
        <van-field v-model="value1" name="pattern" placeholder="请输入手机号">
          <template #button>
            <van-button size="small" v-show="show" class="waw_button" @click="onClick"
              >发送验证码</van-button
            >
            <span v-show="!show" class="count">{{ count }} s</span>
          </template>
        </van-field>
        <!-- 通过 validator 进行函数校验 -->
        <van-field
          v-model="value2"
          name="validator"
          placeholder="请输入验证码"
        />
        <!-- 通过 validator 进行异步函数校验 -->
        <van-field
          v-model="value3"
          name="asyncValidator"
          placeholder="请输入密码"
        />
        <div style="margin: 0.8rem; margin-top: 1rem">
          <van-button round block type="warning" native-type="submit" @click="assa()">
            确定
          </van-button>
        </div>
      </van-form>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import { Toast } from "vant"; //引入文字提示
Vue.use(Toast);
export default {
  data() {
    return {
      value1: "",
      value2: "",
      value3: "",
      pattern: /\d{6}/,
      show: true,
      count: "",
      timer: null,
    };
  },
  methods: {
    // 校验函数返回 true 表示校验通过，false 表示不通过
    validator(val) {
      return /1\d{10}/.test(val);
    },
    // 异步校验函数返回 Promise
    asyncValidator(val) {
      return new Promise((resolve) => {
        Toast.loading("验证中...");

        setTimeout(() => {
          Toast.clear();
          resolve(/\d{6}/.test(val));
        }, 1000);
      });
    },
    onFailed(errorInfo) {
      console.log("failed", errorInfo);
    },
    // 跳转至登录页
    toLogins() {
      this.$router.push("/login");
    },
    onClick() {
    //   //点击发送验证码
    //   var phone = /^[1]([3-9])[0-9]{9}$/;
    //   if (!phone.test(this.value1)) {
    //     Toast({
    //       message: "手机号码格式不正确！！！",
    //       position: "center",
    //     });
    //     return;
    //   } else {
          this.$ClientAPI.VerificationCode({
              mobile:this.value1,
              sms_type:'login'       
          }).then(res=>{
              console.log(res);
          })
    //   },
      const TIME_COUNT = 60;
      if (!this.timer) {
        this.count = TIME_COUNT;
        this.show = false;
        this.timer = setInterval(() => {
          if (this.count > 0 && this.count <= TIME_COUNT) {
            this.count--;
          } else {
            this.show = true;
            clearInterval(this.timer);
            this.timer = null;
          }
        }, 1000);
      }
    },
    assa(){
        this.$axios.post('http://120.53.31.103:84/api/app/smsCode',{
                mobile:this.value1,
                sms_type:getPassword
        }).then((res)=>{
            console.log(res)
        })
    }
  },
};
</script>

